.skill-heading {
  font-size: 3rem;
  line-height: 20px;
  margin-top: 8vh;

  margin-bottom: 8vh;
  text-align: left;
  width: 80%;
}

.skills-container {
  width: 80%;
  display: grid;
  flex-direction: column;
  justify-content: left;


  .skill-img {
    display: flex;
    flex-direction: row;
    height: 80px;
    width:80px
  }

  .skill-title {
    display: flex;
    text-align: left;
    // flex-direction: row;
    margin: 10px;
  }

  .skills-grid {
    display: grid;
    width: 100%;
    justify-content: space-evenly;    
  }

  .skills-grid {
    @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
    *{
      margin: 0;
      padding: 0;
      box-sizing: border-box;
      font-family: 'Poppins', sans-serif;
    }
    html,body{
      display: grid;
      height: 60px;
      place-items: center;
      background: #6665ee;
    }
    ::selection{
      color: #fff;
      background: #6665ee;
    }
    .skill-bars{
      padding: 25px 30px;
      width: 60px;
      background: #fff;
      box-shadow: 5px 5px 20px rgba(0,0,0,0.2);
      border-radius: 10px;
    }
    .skill-bars .bar{
      margin: 20px 0;
    }
    .skill-bars .bar:first-child{
      margin-top: 0px;
    }
    .skill-bars .bar .info{
      margin-bottom: 5px;
    }
    .skill-bars .bar .info span{
      font-weight: 500;
      font-size: 17px;
      opacity: 0;
      animation: showText 0.5s 1s linear forwards;
    }

    .skill-group {
      display: grid;
      margin: 30px;
    }

    @keyframes showText {
      100%{
        opacity: 1;
      }
    }
    .skill-bars .bar .progress-line{
      height: 10px;
      width: 100px;
      background: #f0f0f0;
      position: relative;
      transform: scaleX(0);
      transform-origin: left;
      border-radius: 10px;
      box-shadow: inset 0 1px 1px rgba(0,0,0,0.05),
                  0 1px rgba(255,255,255,0.8);
      animation: animate 1s cubic-bezier(1,0,0.5,1) forwards;
    }
    @keyframes animate {
      100%{
        transform: scaleX(1);
      }
    }
    .bar .progress-line span{
      height: 20px;
      position: absolute;
      margin-top: 10px;
      border-radius: 10px;
      transform: scaleX(0);
      transform-origin: left;
      background: #F5675D;
      animation: animate 1s 1s cubic-bezier(1,0,0.5,1) forwards;
    }
    .bar .progress-line.react span{
      width: 70%;
    }
    .bar .progress-line.ruby span{
      width: 45%;
    }
    .bar .progress-line.dart span{
      width: 60%;
    }
    .bar .progress-line.flutter span{
      width: 60%;
    }
    .bar .progress-line.js span{
      width: 70%;
    }
    .bar .progress-line.firebase span{
      width: 45%;
    }
    .progress-line span::before{
      position: absolute;
      content: "";
      top: -10px;
      right: 0;
      height: 0;
      width: 0;
      border: 7px solid transparent;
      border-bottom-width: 0px;
      border-right-width: 0px;
      border-top-color: #000;
      opacity: 0;
      animation: showText2 0.5s 1.5s linear forwards;
    }
    .progress-line span::after{
      position: absolute;
      top: -28px;
      right: 0;
      font-weight: 500;
      background: #000;
      color: #fff;
      padding: 1px 8px;
      font-size: 12px;
      border-radius: 3px;
      opacity: 0;
      animation: showText2 0.5s 1.5s linear forwards;
    }
    @keyframes showText2 {
      100%{
        opacity: 1;
        margin-left: 10px;

      }
    }
    .progress-line.react span::after{
      content: "90%";
    }
    .progress-line.ruby span::after{
      content: "65%";
    }
    .progress-line.dart span::after{
      content: "80%";
    }
    .progress-line.flutter span::after{
      content: "80%";
    }
    .progress-line.js span::after{
      content: "90%";
    }
    .progress-line.firebase span::after{
      content: "65%";
    }
  }
}
